body {
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
}

.icon {
  vertical-align: middle;
  font-size: 20px;
  line-height: 1px;
}

.icon_header {
  vertical-align: middle;
  font-size: 26px;
  line-height: 1px;
}

.icon, .icon_header {
  margin-right: 5px;
}

.close_icon {
  color: red;
  cursor: pointer;
  margin-top: 10px;
  margin-left: 30px;
}

.grid-container {
  grid-template: "sidebar header header header" .2fr
                 "sidebar main main main" 3fr
                 / 260px 1fr 1fr 1fr;
  height: 100vh;
  display: grid;
}

.header {
  grid-area: header;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 30px;
  display: flex;
  box-shadow: 0 6px 7px -3px #00000059;
}

.menu-icon {
  display: none;
}

#sidebar {
  grid-area: sidebar;
  height: 100%;
  transition: all .5s;
  overflow-y: auto;
}

.sidebar-title {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  padding: 15px 30px 0;
  display: flex;
}

.sidebar-title > span {
  display: none;
}

.sidebar-brand {
  margin-top: 15px;
  font-size: 20px;
  font-weight: 700;
}

.sidebar-list {
  padding: 0;
  list-style-type: none;
}

.sidebar-list-item {
  padding: 20px;
  font-size: 18px;
}

.sidebar-list-item:hover {
  cursor: pointer;
  background-color: #fff3;
}

.sidebar-list-item > a {
  color: #000;
  letter-spacing: 1px;
  font-weight: 500;
  text-decoration: none;
}

.sidebar-responsive {
  position: absolute;
  z-index: 12 !important;
  display: inline !important;
}

.main-container {
  grid-area: main;
  padding: 20px;
  overflow-y: auto;
}

.main-title {
  justify-content: space-between;
  display: flex;
}

.main-cards {
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 20px;
  margin: 15px 0;
  display: grid;
}

.card {
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-around;
  padding: 8px 15px;
  display: flex;
}

.card:first-child {
  background-color: #2962ff;
}

.card:nth-child(2) {
  background-color: #ff6d00;
}

.card:nth-child(3) {
  background-color: #2e7d32;
}

.card:nth-child(4) {
  background-color: #d50000;
}

.card-inner {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card-inner > .card_icon {
  font-size: 25px;
}

.charts {
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  height: 300px;
  margin-top: 60px;
  display: grid;
}

@media screen and (max-width: 992px) {
  .grid-container {
    grid-template: "header" .2fr
                   "main" 3fr
                   / 1fr;
  }

  #sidebar {
    display: none;
  }

  .menu-icon, .sidebar-title > span {
    display: inline;
  }
}

@media screen and (max-width: 768px) {
  .main-cards {
    grid-template-columns: 1fr;
    gap: 10px;
    margin-bottom: 0;
  }

  .charts {
    grid-template-columns: 1fr;
    margin-top: 30px;
  }
}

@media screen and (max-width: 576px) {
  .hedaer-left {
    display: none;
  }
}

.modal:has(.modal-login) {
  background: #fff;
}

.patient-card {
  border-radius: 5px;
  font-size: 20px;
}

.profile-image {
  width: 500px;
  height: 600px;
  overflow: hidden;
  background-position: center !important;
  background-repeat: no-repeat !important;
  background-size: cover !important;
}

.patient-card h2 {
  margin: 20px 0;
  font-size: 1.5rem;
}

.patient-card p {
  margin: 5px 0;
}

.viewTable td {
  text-align: left;
}

.section_header, .patient-card > h5 {
  margin: 60px 0 20px;
  font-size: 25px;
  font-weight: bold;
}
